import React, { useState } from "react"
import "./App.css"
import { PDFDocument, StandardFonts } from "pdf-lib"

function App() {
    const [imageFile, setImageFile] = useState(null)

    const handleImageUpload = (e) => {
        setImageFile(e.target.files[0])
    }

    const createPdf = async (e) => {
        e.preventDefault()

        const pdfDoc = await PDFDocument.create()
        const pageWidth = 595.28
        const pageHeight = 841.89 // A4 dimensions in points

        if (imageFile) {
            const reader = new FileReader()
            reader.onload = async () => {
                const imgBytes = new Uint8Array(reader.result)
                const img = await pdfDoc.embedJpg(imgBytes)

                const imgWidth = img.width
                const imgHeight = img.height

                const scaleFactor = pageWidth / imgWidth
                const scaledImgHeight = imgHeight * scaleFactor

                let yOffset = 0

                while (yOffset < imgHeight) {
                    const page = pdfDoc.addPage([pageWidth, pageHeight])

                    page.drawImage(img, {
                        x: 0,
                        y:
                            pageHeight -
                            (yOffset + pageHeight / scaleFactor) * scaleFactor,
                        width: pageWidth,
                        height: scaledImgHeight,
                        clip: {
                            x: 0,
                            y: yOffset,
                            width: imgWidth,
                            height: pageHeight / scaleFactor,
                        },
                    })

                    yOffset += pageHeight / scaleFactor
                }

                const pdfBytes = await pdfDoc.save()
                const blob = new Blob([pdfBytes], { type: "application/pdf" })
                const url = URL.createObjectURL(blob)
                const a = document.createElement("a")
                a.href = url
                a.download = "document.pdf"
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
            }
            reader.readAsArrayBuffer(imageFile)
        } else {
            const page = pdfDoc.addPage([pageWidth, pageHeight])
            const font = await pdfDoc.embedFont(StandardFonts.TimesRoman)

            // Sample text to fill the page
            let text =
                "This text will fill the entire A4 page. You can adjust the font size and spacing to fit your content."
            const fontSize = 10
            const lineHeight = 12

            let y = page.getHeight() - fontSize
            const textWidth = page.getWidth() / fontSize

            while (y > fontSize && text.length > 0) {
                const textToDraw = text.substring(0, textWidth)
                text = text.substring(textWidth)

                page.drawText(textToDraw, {
                    x: 0,
                    y: y,
                    size: fontSize,
                    font,
                })

                y -= lineHeight
            }

            const pdfBytes = await pdfDoc.save()
            const blob = new Blob([pdfBytes], { type: "application/pdf" })
            const url = URL.createObjectURL(blob)
            const a = document.createElement("a")
            a.href = url
            a.download = "document.pdf"
            document.body.appendChild(a)
            a.click()
            document.body.removeChild(a)
        }
    }

    const buttonStyle = {
        margin: "10px",
        padding: "10px 20px",
        fontSize: "16px",
        color: "#fff",
        backgroundColor: "#007BFF",
        border: "none",
        borderRadius: "5px",
        cursor: "pointer",
    }

    const inputStyle = {
        margin: "10px",
        padding: "10px 20px",
        fontSize: "16px",
    }

    return (
        <div className="App">
            <header className="App-header">
                <input
                    type="file"
                    accept="image/*"
                    onChange={handleImageUpload}
                    style={inputStyle}
                />
                <button onClick={(e) => createPdf(e)} style={buttonStyle}>
                    criar pdf da imagem escolhida
                </button>
            </header>
        </div>
    )
}

export default App
